.Counter {
  /* 레이아웃 */
  width: 10rem;
  height: 10rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 1rem;

  /* 색상 */
  color: white;

  /* 폰트 */
  font-size: 3rem;

  /* 기타 */
  border-radius: 100%;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-transition: background-color 0.75s;
  -o-transition: background-color 0.75s;
  transition: background-color 0.75s;
}



